@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');
.App
{
    text-align: center;
    background-color: #ffff;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100vh;
}
body{
    margin:0px;
    padding: 0px;
    background-color: #ffffff;
    font-family: 'Montserrat', sans-serif;
}
*{
    box-sizing: border-box;
    scroll-behavior: smooth;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
button{
    outline: none;
    border: none;
}
input{
    outline: none;
    border: none;
}
.logo img{
    width: 70px;
}
#main{
    width:100%;
    height:768px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    /* background-image: url('./images/background33.jpeg'); */
    background-image: url('./images/pexels-nikolaos-dimou-1319459.jpg');
    background-size: cover !important;
}
nav{
	display: flex;
	justify-content: space-around;
	align-items: center;
	left: 0;
	top: 0;
	width:100%;
	z-index: 1;
    background-color: transparent;
    transition: 0.6s ease-in-out;
}
nav.active{
	box-shadow: 5px 10px 30px rgba(0, 247, 255, 0.157);
	background-color: #171d2b;
}
nav ul{
	display: flex;
}
.active{
    background-color: #3e68d9;
    color:#ffffff;
	/* box-shadow: 5px 10px 30px rgba(189, 198, 64, 0.411); */
	transition: all ease 0.2s;
}
nav ul li a{
    font-family: calibri;
	height:40px;
	line-height: 40px;
	margin: 3px;
    margin-top: 12px;
	padding: 0px 22px;
	display: flex;
	font-size: 0.9rem;
	text-transform: uppercase;
	font-weight: 500;
	color:#ffff;
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.2s ease-in-out;
    text-decoration: none;
}
nav ul li a:hover{
	background-color: #3e68d9;
    color:#ffffff;
	box-shadow: 5px 10px 30px rgba(43, 67, 222, 0.336);
	transition: all ease 0.2s;
}
nav .menu-btn,
.menu-icon{
		display:none;
	}
.name{
	font-family: 'Montserrat';
	width: 500px;
	position: absolute;
    right:5%;
	top: 50%;
	transform: translate(-22%,-50%);
    user-select: none;
}
.name span{
    color: #ffff;
    font-size:2rem;
    /* background: #171d2b5e; */
}
.name .details{
    color: #b4b4b4;
}
.name button
{
    width:110px;
    height: 40px;
    justify-content: center;
    align-items: center;
    background-color: #171d2b;
    box-shadow: 5px 10px 30px rgba(43, 67, 222, 0.336);
    border-radius: 10px;
    letter-spacing: 1px;
    transition: 0.2s ease-in-out;
    color: #3e68d9;
    transition: 0.6s ease-in-out ;
    -webkit-transition: 0.6s ease-in-out ;
    -moz-transition: 0.6s ease-in-out ;
    -ms-transition: 0.6s ease-in-out ;
    -o-transition: 0.6s ease-in-out ;
}
.name button:hover
{
    color: #ffff;
    border: 1px solid white;
}
.name h1{
	font-family:'Montserrat';
	font-size: 40px;
	margin:0px;
	letter-spacing: 2px;
	color:#000000;
    user-select: none;
}

#features{
    width:100%;
    height:100vh;
    box-sizing: border-box;
    font-family: calibri;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px ;
    background: url('./images/backgroundf.jpg');
}
.a-container{
    display: flex;
    justify-content: center;
    align-content: center;


}
.a-box{
    background-color: #ffffff;
    width:300px;
    height: 450px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 10px 12px rgba(0,0,0,0.1);
    transition: 0.3s ease-in-out;
    position: relative;
}
.a-b-img{
    width: 100%;
    height: 60%;

}
.a-b-img img{
    padding: 15px;
    width:100%;
    height: 100%;

}
.a-box:hover{
    box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
}
#presentaion{
    padding-top: 30px;
    width:100%;
    height:768px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    /* background-image: linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.3)), url(images/presentation.png); */
    /* background-image: url(images/presentation.png); */
    text-align: center;
}
#presentaion h1{
font-size: 2rem;
user-select: none;
}
#services{
    width:100%;
    height:100vh;
    box-sizing: border-box;
    font-family: calibri;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px ;
    
}

.s-heading h1{
    color:#FFFFFF;
    font-size: 3rem;
    margin: 0px;
    padding: 0px;

}
.s-heading p{
    color:rgba(247, 247, 247, 0.678);
    font-size: 1rem;
    margin: 0px;
    padding: 0px;
}
.s-heading{
    text-align: center;
    margin: 20px 0px;
}
.s-box{
    background-color: #ffffff;
    width:350px;
    height: 470px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
    position: relative;
}
.b-container{
    display: flex;
    justify-content: center;
    align-content: center;


}
.s-b-img{
    width: 100%;
    height: 50%;

}
.s-b-img img{
    width:100%;
    height: 100%;

}
.s-b-text{
    width: 100%;
    height: 40%;
    background-color:#ffffff;
    display:flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
}
.s-b-text p{
    margin: 0px;
    color:#000266a6;
    font-size: 1.1rem;
    font-family: calibri;
    display: block;
    display: -webkit-box;
    max-width: 80%;
    margin-top: 25px;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.s-b-text p:hover{
    color:#000266;
}
.s-b-text a{
    margin-top:15px ;
}
.s-type{
    font-family: calibri;
    color:#000266;
    background-color: #ffffff;
    padding: 10px 15px;
    position: absolute;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 18px;
    border-radius: 0px 0px 10px 10px;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.2);
}



@media(max-width:1190px){
	#main{
		background-size: 1150px !important;
		
	}
    .App
    {
        background-size: 1150px ;
        background-repeat: no-repeat;
    }
	.name{
		left: 50%;
		top: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
	}
	.about-image img{
		height:400px;
    }
    #features{
		height:auto;
    }
    .a-container{
		flex-wrap: wrap;
	}
    .a-box{
		flex-grow: 1;
	}
    #services{
		height:auto;
    }
    .b-container{
		flex-wrap: wrap;
	}
    .s-box{
		flex-grow: 1;
	}
	.s-b-img img{
		object-fit:cover;
    }
    .a-b-img img{
		object-fit:contain;
    }
    
    

}
.nn
{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    min-height: 35vh;
    background: transparent;
    width: 100%;
}
footer
{
    position: relative;
    width: 100%;
    background: #171d2b;
    min-height: 100px;
    padding: 20px 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
footer ul.icons,
footer ul.timeline
{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
    gap: 10px;
    flex-wrap: wrap;
    
}
footer.icons li,
footer.timeline
{
    list-style: none;
}
footer ul.icons li a
{
    font-size: 2em;
    color: #ffffff;
    display: inline-block;
    transition: 0.5s ease-out;
}
footer ul.icons li a:hover
{
    transform: translateY(-10px);
    color: #3e68d9;
}
footer ul.timeline li a
{
    color: #ffffff;
    display: inline-block;
    transition: 0.5s ease-out;
    opacity: 0.80;
    font-weight: 400;
}
footer ul.timeline li a:hover
{
    color: #3e68d9;
    opacity: 1;
}
footer p
{
    color:#ffffff;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 14px;
}
footer span
{
    color: #ffff;
}
/* --- */
@media(max-width:970px){
    nav
    {
        width: 100%;
    } 
	.main{
		background-image: none !important;
		
	}
	.name{
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
	}
	.about-image{
        display:none;
    }
    .cv-btn{
        margin-left: 27%;
        margin-top: 20px;
    }
	#about{
		justify-content: center;
        padding-top: 0px;
        text-align: center;
        
        margin-top: 0px;
	}
	.about-text{
        width: 90%;
	}
	.about-text h1{
		font-size:4rem;
    }
    .about-text p{
        width: 100%;
    }
    #features{
        margin-bottom: 100px;
    }
	#presentaion{
        display: none;
    }
}
@media(max-width:600px){
	.name{
		width:60%;
    }
    }
    .scroll{
        left: 42.5%;
    }
@media(max-width:1100px)
    {
        .menu-icon
        {
        display:block;
        }
        nav{
            justify-content: space-between;
            height: 65px;
            padding: 0px 30px;
            width: 100%;
        }
        .logo img{
            width:70px;
            margin-left: 20px;
        }
        .header-btns{
            margin: 0;
        }
        .menu{
            display:none;
            position:absolute;
            top:65px;
            left:0px;
            background-color: #171d2b;
            border-bottom:4px solid #3e68d9;
            width:100%;
            padding:0px;
            margin:0px;
        }
        .menu li{
            width:100%;
        }
        nav .menu li a{
            width:100%;
            height:40px;
            justify-content: center;
            align-items: center;
            margin:0px;
            padding: 25px;
            border:1px solid rgba(38,38,38,0.03);
        }
        nav .menu-icon{
            cursor:pointer;
            float:right;
            padding:28px 20px;
            position:relative;
            user-select: none;
            color: #ffff;
        }
        nav .menu-icon .nav-icon{
            background-color:#ffff;
            display:block;
            height:2px;
            position:relative;
            transition: background 0.2s ease-out;
            width:18px;
        }
        nav .menu-icon .nav-icon:before,
        nav .menu-icon .nav-icon:after{
            background:#fff;
            content:'';
            display:block;
            height:100%;
            position:absolute;
            transition:all ease-out 0.2s;
            width:100%;
        }
        nav .menu-icon .nav-icon:before{
            top:5px;
        }
        nav .menu-icon .nav-icon:after{
            top:-5px;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon{
            background:transparent;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:before{
            transform: rotate(-45deg);
            top:0;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:after{
            transform: rotate(45deg);
            top:0;
        }
        nav .menu-btn{
            display:none;
        }
        nav .menu-btn:checked ~ .menu{
            display:block;
        }
    }
        @media(max-width:400px){
            .s-box{
                width: 100%;
                height:400px;
            }
            #contact
            {
                width: 100%;
                height: inherit;
                padding-bottom: 90px;
                margin-bottom: 100px;
            }
        }
/* Scroll Bar Style */
/* width */
    ::-webkit-scrollbar {
        width: 10px;
    }
    
    /* Track */
    ::-webkit-scrollbar-track {
        background: #3e68d9;
    }
    
    /* Handle */
    ::-webkit-scrollbar-thumb {
        background: #999;
    }
    
    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: #555;
    }
    /* Loader Style */
    #Loader
    {
        text-align: center;
        display:flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        width: 100%;
        height: 100vh;
        background-color: #171d2b;
        color: #3e68d9;
    }