@import url('https://fonts.googleapis.com/css2?family=Bellota+Text:wght@300;400;700&family=Montserrat:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500;600;700&display=swap');
.aboutb
{
    font-family: 'Bellota Text', cursive;
    font-family: 'Cairo', sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    /* min-height: 100vh; */
    width: 100%;
    background-color: #f3f3f7;
}
.containerabout
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.containerabout .card
{
    width: 330px;
    height:416px ;
    padding: 60px 30px;
    margin: 20px;
    background: #f3f3f7;
    box-shadow: 0.6em 0.6em 1.2em #d2dce9,                -0.5em 0.5em 1em #ffff;
    border-radius: 20px;
}
.containerabout .card .content
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.containerabout .card .content .imgBx
{
    width: 180px;
    height: 180px;
    border-radius: 50%;
    position: relative;
    margin-bottom: 20px;
    overflow: hidden;
}
.containerabout img
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.containerabout h4
{
    color: #36187d;
    font-size: 1.7rem;
    font-weight: bold;
    text-align: center;
    letter-spacing: 1px;
}
.containerabout  h5
{
    color: #6c758f;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
}
.containerabout .sci
{
    margin-top: 20px;
}
.containerabout .sci a
{
    text-decoration: none;
    color: #6c758f;
    font-size: 30px;
    margin: 10px;
    transition: 0.4s ease-in-out;
}
.containerabout .sci a:hover
{
    color: #36187d;
}